class Arg {
  constructor(name, value, type = false) {
    this.name = name;
    this.value = value;
    this.type = type;
  }
  getName() {
    return this.name;
  }
  getValue() {
    return this.value;
  }
  getTypeCheckbox() {
    return this.type;
  }

  setValue(newValue) {
    this.value = newValue;
  }
}

class Opr {
  constructor(id, label, doc, args = []) {
    this.value = id;
    this.label = label;
    this.doc = doc;
    this.args = args;
  }

  getLabel() {
    return this.label;
  }

  getDoc() {
    return this.doc;
  }

  getArgs() {
    return this.args;
  }

  exec() {
    return null;
  }
}

export class OprLength extends Opr {
  constructor() {
    super(
      1,
      "Length",
      "La longueur d'une chaine de caractÃ¨res, exprimÃ©e en nombre de points de code UTF-16",
      [new Arg("NoCount", "")]
    );
  }

  exec(str) {
    return str.replaceAll(this.args[0].getValue(), "").length;
  }
}

export class OprCount extends Opr {
  constructor() {
    super(2, "Count", "Conte le nombre de chaine CountString dans Input", [
      new Arg("CountString", ""),
    ]);
  }

  exec(str) {
    const regexp = new RegExp(this.args[0].getValue(), "g");
    return [...str.matchAll(regexp)].length;
  }
}

