      "renvoie l'indice de la premiÃ¨re occurence de la valeur cherchÃ©e au sein de la chaÃ®ne courante. Elle renvoie -1 si la valeur cherchÃ©e n'est pas trouvÃ©e",
      [new Arg("String", "")]
