export class OprToLocaleUpperCase extends Opr {
  constructor() {
    super(
      7,
      "ToLocaleUpperCase",
      "Renvoie une conversion en majuscules de la chaÃ®ne de caractÃ¨res courante",
      []
    );
  }

  exec(str) {
    return str.toLocaleUpperCase();
  }
}

export class OprToLocaleCapitalizeCase extends Opr {
  constructor() {
    super(
      8,
      "ToLocaleCapitalizeCase",
      "Renvoie une conversion de la premiÃ¨re lettre de chaque mot en capitale. Les autres caractÃ¨res ne seront pas modifiÃ©s et conserveront ainsi la forme originale",
      []
    );
  }

  exec(str) {
    let res = "";
    str.split(" ").forEach((e) => {
      e = e.replace(e[0], e[0].toLocaleUpperCase());
      res += e + " ";
    });
    return res;
  }
}

export class OprTrim extends Opr {
  constructor() {
    super(
      9,
      "OprTrim",
      "Retire les blancs en plus de la chaÃ®ne de caractÃ¨res",
      []
    );
  }

  exec(str) {
    let res = str.trim();
    res = res.replaceAll(/\t+/g, "\t");
    res = res.replaceAll(/[ ]+/g, " ");
    res = res.replaceAll(/\n+/g, "\n");
    return res;
  }
}

